import {get, post} from '@/util/axios'

/**
 * 分页查询接口
 * @param params
 * @returns {Promise<unknown>}
 */
export function queryArticle(params) {
    return get('/article/selectAll', params)
}

/**
 * 查询详情
 * @param params
 * @returns {Promise | Promise<unknown>}
 */
export function queryArticleDetail(params) {
    return get('/article/queryDetail', params)
}


/**
 * 新增、修改接口
 * @param params
 * @returns {Promise<unknown>}
 */
export function insertArticle(params) {
    return post('/article/insert', params)
}


/**
 * 修改文章
 * @param params
 * @returns {Promise<unknown>}
 */
export function updateArticle(params) {
    return post('/article/insert', params)
}

/**
 * 删除接口
/**
 * 删除接口
 * @param params
 * @returns {Promise<unknown>}
 */
export function deleteArticle(params) {
    // 检查本地存储中是否存在 token
    const token = window.localStorage.getItem("token");
  
    // 设置默认的请求头对象
    const headers = {};

    // 如果 token 存在，则将其添加到请求头中
    if (token) {
        headers['token'] = token;
    }

    // 发送请求
    return post('/article/deleteBatch', params, {
        headers: headers
    });
}



