<template>
  <!--分页-->
  <el-pagination
      style="margin-top: 10px"
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="current"
      :page-sizes="[10, 20, 30, 40]"
      :page-size=size
      layout="total, sizes, prev, pager, next, jumper"
      :total=total>
  </el-pagination>
</template>

<script>
export default {
  name: "HandelPage",
  data(){
    return{
      current:1,
      size:10,
      total:0
    }
  },
  methods:{
    /**
     * 页数编码改变
     * @param val
     */
    handleSizeChange(val) {
      this.size = val;
      this.current = 1;
      this.$emit('queryInfo');
    },
    /**
     * 当前改变
     * @param val
     */
    handleCurrentChange(val) {
      this.current = val;
      this.$emit('queryInfo');
    },
  }
}
</script>

